body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: whitesmoke;
}

.navbar {
    height: 100px;
    /* background-color: #333; */
    /* color: whitesmoke; */
    color: #333;
    margin: 15px 0;
    text-align: center;
    font-size: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}

.navbar div,
.navbar small {
    width: 100%;
}

.todo-list {
    background-color: white;
    margin: auto;
    width: 60%;
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 1px solid #efefef;
    box-shadow: /* The top layer shadow */
    0 1px 1px rgba(0, 0, 0, 0.15), /* The second layer */
    0 10px 0 -5px #eee, /* The second layer shadow */
    0 10px 1px -4px rgba(0, 0, 0, 0.15), /* The third layer */
    0 20px 0 -10px #eee, /* The third layer shadow */
    0 20px 1px -9px rgba(0, 0, 0, 0.15);
    padding: 30px;
}

.todo-item {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 30px 20px 0;
    width: 70%;
    border-bottom: 1px solid #cecece;
    font-family: Roboto, sans-serif;
    font-weight: 100;
    font-size: 15px;
    color: #333333;
}

input[type=checkbox] {
    margin-right: 10px;
    font-size: 30px;
}

input[type=checkbox]:focus {
    outline: 0;
}

.jokes {
    margin: auto;
    width: 50%;
    color: #333;
    margin: 40px auto 15px;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 1px solid #efefef;
    background-color: white;
    padding: 15px;
    background-color: white;
    box-shadow: /* The top layer shadow */
    0 1px 1px rgba(0, 0, 0, 0.15);
}

.jokes span {
    font-size: 26px;
}


/* todo form */

.new-todo-form {
    text-align: center;
    background-color: whitesmoke;
}

.new-todo-form form {
    display: flex;
    justify-content: center;
    align-content: center;
    margin: 10px auto 20px;
    height: 40px;
    width: 60%;
}

.new-todo-form form input {
    width: 60%;
    margin-right: 20px;
    transition: all 0.3s ease;
    border: none;
    border-bottom: 2px solid #888888;
    background-color: transparent;
    outline: none;
    font-size: 1.5em;
}

.new-todo-form form button {
    text-decoration: none;
    color: white;
    background-color: #111;
    letter-spacing: 2px;
    padding: 7px 17px;
    border: 5px solid #111;
    transition: .2s;
    cursor: pointer;
    border-radius: 10px;
}

.new-todo-form form button:hover {
    box-shadow: inset 0px 0px 0px 2px white;
}

.footer {
    margin-top: 5vh;
    padding: 10px;
    border-top: 2px solid lightgray;
    text-align: center;
    font-family: Arial, Helvetica, sans-serif;
}

.footer a {
    text-decoration: none;
    margin: 0 10px;
}